<template>
	<div class="m-l-50 m-t-30 w-500">
		<el-form ref="form" :model="form" :rules="rules" label-width="130px">
			<el-form-item label="" prop="username">
				<el-input v-model.trim="form.username" class="h-40 w-200" :maxlength=12></el-input>
			</el-form-item>
			<el-form-item label="" prop="password">
				<el-input v-model.trim="form.password" class="h-40 w-200"></el-input>
			</el-form-item>
			<el-form-item label="" prop="realname">
				<el-input v-model.trim="form.realname" class="h-40 w-200"></el-input>
			</el-form-item>
      <el-form-item label="" prop="mobile">
				<el-input v-model="form.mobile" class="h-40 w-200"></el-input>
			</el-form-item>
      <el-form-item label="" prop="email">
				<el-input v-model.trim="form.email" class="h-40 w-200"></el-input>
			</el-form-item>
			<!-- <el-form-item label="" prop="structure_id">
				<el-select v-model="form.structure_id" placeholder="" class="w-200">
					<el-option v-for="(item,index) in orgsOptions" :label="item.title" :value="item.id" :key='index'></el-option>
				</el-select>
			</el-form-item>
      <el-form-item label="" prop="post_id">
				<el-select v-model="form.post_id" placeholder="" class="w-200">
					<el-option v-for="(item,index) in positionsOptions" :label="item.name" :value="item.id" :key='index'></el-option>
				</el-select>
			</el-form-item> -->
			<el-form-item label="">
				<el-input v-model.trim="form.remark" class="h-40 w-200"></el-input>
			</el-form-item>
			<el-form-item label="">
				<el-checkbox-group v-model="selectedGroups">
					<el-checkbox v-for="item in groupOptions" :label="item.title" class="form-checkbox"></el-checkbox>
				</el-checkbox-group>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" @click="add('form')" :loading="isLoading"></el-button>
				<el-button @click="goback()"></el-button>
			</el-form-item>
		</el-form>
	</div>
</template>
<style type="text/css">
	.form-checkbox:first-child{
		margin-left: 15px;
	}
</style>
<script>
  import http from '../../../../assets/js/http'
  import fomrMixin from '../../../../assets/js/form_com'

  export default {
    data() {
      return {
        isLoading: false,
        form: {
          username: '',
          password: '',
          realname: '',
          mobile:0,
          email:'',
          structure_id: null,
          post_id:null,
          remark: '',
          groups: []
        },
        orgsOptions: [],
        positionsOptions:[],
        groupOptions: [],
        selectedGroups: [],
        selectedIds: [],
        rules: {
          username: [
            { required: true, message: '' }
          ],
          password: [
            { required: true, message: '' }
          ],
          realname: [
            { required: true, message: '' }
          ],
          mobile: [
            { required: true, message: '' }
          ],
          structure_id: [
            { required: true, message: '' }
          ]
        }
      }
    },
    methods: {
      selectCheckbox() {
        let temp = false
        _(this.groupOptions).forEach((res) => {
          if (this.selectedGroups.toString().indexOf(res.title) > -1) {
            this.selectedIds.push(res.id)
          }
        })
        if (this.selectedIds.length) {
          this.form.groups = _.cloneDeep(this.selectedIds)
          temp = true
        }
        this.selectedIds = []
        return temp
      },
      add(form) {
        if (!this.selectCheckbox()) {
          _g.toastMsg('warning', '')
          return
        }
        console.log('res = ', _g.j2s(this.form))
        this.$refs.form.validate((pass) => {
          if (pass) {
            this.isLoading = !this.isLoading
            this.apiPost('admin/users', this.form).then((res) => {
              this.handelResponse(res, (data) => {
                _g.toastMsg('success', '')
                _g.clearVuex('setUsers')
                setTimeout(() => {
                  this.goback()
                }, 1500)
              }, () => {
                this.isLoading = !this.isLoading
              })
            })
          }
        })
      },
      getAllGroups() {
        this.apiGet('admin/groups').then((res) => {
          this.handelResponse(res, (data) => {
            this.groupOptions = data
          })
        })
      },
      getAllPositions() {
        this.apiGet('admin/posts').then((res) => {
          this.handelResponse(res, (data) => {
            this.positionsOptions = data
          })
        })
      },
      getAllOrgs() {
        this.apiGet('admin/structures').then((res) => {
          this.handelResponse(res, (data) => {
            this.orgsOptions = data
          })
        })
      }
    },
    created() {
      this.getAllGroups()
      //this.getAllOrgs()
      //this.getAllPositions()
    },
    mixins: [http, fomrMixin]
  }
</script>


