<template>
  <div class="m-l-50 m-t-30 w-900">
    <el-form ref="form" :model="form" :rules="rules" label-width="130px">
      <el-form-item label="" prop="name" >
        <el-input v-model.trim="form.name" class="h-40 w-200" disabled></el-input>
      </el-form-item>
      <el-form-item label="" prop="category_id">
        <el-select v-model="form.category_id" placeholder="" class="w-200">
          <el-option v-for="(item,index) in options" :label="item.name" :value="item.id" :key='index'></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="price">
        <el-input v-model.number="form.price" placeholder="" class="h-40 w-200"></el-input>
        <span></span>
      </el-form-item>
      <el-form-item label="" prop="preferential_price">
        <el-input v-model.number="form.preferential_price" class="h-40 w-200" placeholder=""></el-input>
        <span></span>
      </el-form-item>
      <el-form-item label="" prop="sale_nums">
        <el-input v-model.number="form.sale_nums" class="h-40 w-200"></el-input>
      </el-form-item>
      <el-form-item label="" prop="recommend">
        <el-rate v-model="form.recommend" class="h-40 w-200"></el-rate>
      </el-form-item>
      <el-form-item label="" prop="image">
        <el-upload
          :action="uploadUrl"
          name='image'
          type="drag"
          :headers="uploadHeaders"
          :show-file-list="false"
          :on-success="handleUploadSuccess"
          :with-credentials='withCredentials'
          >
          <img v-if="currentImage" :src="currentImage" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="" prop="introduction">
        <el-input v-model.trim="form.introduction"  type="textarea" :rows="3"></el-input>
      </el-form-item>
      <el-form-item label="" prop="detail">
        <vue-ueditor-wrap ref="ueditor" v-model="form.detail" :destroy="false" :config="config" @ready="ready"></vue-ueditor-wrap>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model.trim="specName" class="h-40 w-200" placeholder=""></el-input>
        <el-button type="text" @click="addSpecName" style='margin-left:20px;'></el-button>
      </el-form-item>
      <el-form-item 
        v-for="(item, index) in attributions"
        :label="item.specName"
        :key="index"
        :prop="item.specName"
      >
        <el-checkbox-group v-model="item.specValueChecked" @change="handleCheckedValueChange" class="h-40 w-800">
          <el-checkbox v-for="(item1,index1) in item.specValue" :label="item1" :key="index1">{{item1}}</el-checkbox>
          <el-button type="text" style='margin-left:100px;' v-if="item.specValue.length>0" @click="deleteSpec(index)">{{item.specName}}</el-button>
          <el-button type="text" @click="addSpecValue(index)">{{item.specName}}</el-button>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item v-if="sku.length>0">
        <el-table
          :data="sku"
          style="width: 100%">
          <el-table-column v-for="(item,index) in sku[0].specName" :key='index'
            :label="item"
            width="80"
          >
            <template slot-scope="scope">
              <span >{{ scope.row.specValue[index] }}</span>
            </template>
          </el-table-column>
          <el-table-column label="()">
            <template slot-scope="scope">
              <el-input v-model="scope.row.price" placeholder=""></el-input>
            </template>
          </el-table-column>
          <el-table-column label="()">
            <template slot-scope="scope">
              <el-input v-model="scope.row.preferential_price" placeholder=""></el-input>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-upload
                :action="uploadSpecUrl+'?index='+scope.$index"
                name='imageSpec'
                :headers="uploadHeaders"
                :show-file-list="false"
                :on-success="handleUploadSpecSuccess"
                :with-credentials='withCredentials'
              > 
                <img v-if="scope.row.image" :src="host+'/'+scope.row.image" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="edit('form')" :loading="isLoading"></el-button>
        <el-button @click="goback()"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  import http from '../../../../assets/js/http'
  import fomrMixin from '../../../../assets/js/form_com'
  import VueUeditorWrap from 'vue-ueditor-wrap'
  export default {
    data() {
      return {
        host:window.HOST,
        isLoading: false,
        withCredentials:true,
        attributions:[],
        specName:null,
        form: {
          id: null,
          name: '',
          price: 0,
          image: null,
          detail: '',
          introduction: '',
          attributions: '',
          category_id: null,
          sale_nums:0,
          status:1,
          recommend:0,
          preferential_price: 0,
          postSpec:[]
        },
        sku:[
          {
            specName:[],
            specValue:[],
            price:0,
            preferential_price:0,
            image:null
          }
        ],
        options: [{ pid: 0, name: '' }],
        uploadUrl:null,
        uploadSpecUrl:null,
        uploadHeaders:{
          authKey:Lockr.get('authKey'),
          sessionId:Lockr.get('sessionId')
        },
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ],
          price:[
            { required: true, message: '', trigger: 'blur' },
            { type: 'number', message: '' },
          ],
          preferential_price:[
            { type: 'number', message: '' },
          ],
          sale_nums:[
            { type: 'number', message: '' },
          ],
          category_id: [
            { required: true, message: '' }
          ]
        },
        config: {
          // 
          autoHeightEnabled: true,
          // 
          initialFrameHeight: 240,
          // 
          initialFrameWidth: '100%',
          // 
          serverUrl: null,
          // UEditor  vue-cli vue-ueditor-wrap 2
          // UEDITOR_HOME_URL: '/static/UEditor/'
          // Request Headers,https://github.com/HaoChuan9421/ueditor/commits/dev-1.4.3.3
          headers: {
            authKey:Lockr.get('authKey'),
            sessionId:Lockr.get('sessionId')
          }
        }
      }
    },
    methods: {
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      handleDelete(index, row) {
        this.sku.splice(index,1)
      },
      addSpecName(){
        if(this.specName===null){
          this.$message.error('');
          return false
        }
        this.attributions.push({
          specName:this.specName,
          specValue:[],
          specValueChecked:[]
        })
        this.specName=null
      },
      addSpecValue(index){
        let _self=this
        let spec=this.attributions[index]
        this.$prompt(''+spec.specName, '', {
          confirmButtonText: '',
          cancelButtonText: '',
        }).then(({ value }) => {
          _self.attributions[index].specValue.push(value)
          _self.attributions[index].specValueChecked.push(value)
        }).catch(() => {
                 
        });
      },
      deleteSpec(index){
        let _self=this;
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          _self.attributions.splice(index,1)
        }).catch(() => {
                  
        });
      },
      handleCheckedValueChange(value){
        this.sku = this.getSku()
      },
      ready (editorInstance) {
       
      },
      parseSku(){
        let postSpec=[]
        if(this.sku.length>0){
          let attributions =  this.sku.filter(function(item){
            return item.price > 0
          })
          if(attributions.length > 0){
            postSpec=attributions
          }
        }
        return postSpec
      },
      edit(form) {
        if(this.attributions.length>0){
          let specImageChecked=[]
          first:
          for(let i=0;i<this.attributions[0].specValueChecked.length;i++){
            second:
            for(let j=0;j<this.sku.length;j++){
              third:
              for(let m=0;m<this.sku[j].specValue.length;m++){
                if(this.sku[j].specValue[m] == this.attributions[0].specValueChecked[i]){
                  specImageChecked.push(this.sku[j].image)
                  break second;
                }
              }
            }
          }
          this.attributions[0].specImageChecked=specImageChecked
        }
        this.$refs[form].validate((valid) => {
          if (valid) {
            this.isLoading = !this.isLoading
            this.form.attributions=this.attributions
            this.form.postSpec=this.parseSku()
            this.apiPut('order/menus/', this.form.id, this.form).then((res) => {
              this.handelResponse(res, (data) => {
                _g.toastMsg('success', '')
                setTimeout(() => {
                  this.goback()
                }, 1500)
              }, () => {
                this.isLoading = !this.isLoading
              })
            })
          }
        })
      },
      handleUploadSuccess(response, file, fileList) {
        this.handelResponse(response, (data) => {
          this.form.image = data
        })
      },
      handleUploadSpecSuccess(response, file, fileList) {
        this.handelResponse(response, (data) => {
          let item = JSON.parse(JSON.stringify(this.sku[data.index]))
          item.image=data.path
          this.$set(this.sku, data.index, item)
        })
      },
      getMenuCategory() {
        this.apiGet('order/category').then((res) => {
          this.handelResponse(res, (data) => {
            _(data).forEach((ret) => {
              ret.id = ret.id.toString()
            })
            this.options = this.options.concat(data)
          })
        })
      },
      getMenuInfo() {
        this.form.id = this.$route.params.id
        this.apiGet('order/menus/' + this.form.id).then((res) => {
          this.handelResponse(res, (data) => {
            data.category_id = data.category_id.toString()
            data.recommend = parseInt(data.recommend )
            this.attributions = data.attributions?JSON.parse(data.attributions):[]
            this.form=data   
            this.sku=data.specs
          })
        })
      },
      //
      multiCartesian(sku,newAttr) {
        let ret=[];
        sku=sku.concat([])
        for(var m=0;m<sku.length;m++){
          if(newAttr.specValueChecked.length>0){
            let specName=sku[m].specName.concat([])
            let tempSpecName = specName.concat([])
            let specValue=sku[m].specValue.concat([])
            let tempSpecValue = specValue.concat([])
            for(var j=0;j<newAttr.specValueChecked.length;j++){
              specName = tempSpecName.concat([])
              specValue = tempSpecValue.concat([])
              specName.push(newAttr.specName)
              specValue.push(newAttr.specValueChecked[j])
              ret.push({
                specName:specName,
                specValue:specValue,
              })
            }
          }else{
            ret = sku
          }
        }
        return ret
      },
      getSku(){
        let attributions=JSON.parse(JSON.stringify(this.attributions))
        let sku=[];
        if(attributions.length >= 1){
          for(var i=0;i<attributions[0].specValueChecked.length;i++){
            sku.push({
              specName:[attributions[0].specName],
              specValue:[attributions[0].specValueChecked[i]],
              price:attributions[0].price,
              preferential_price:attributions[0].preferential_price,
              image:attributions[0].image
            })
          }
          if(attributions.length > 1){
            for(var i=1; i< attributions.length; i++){
              sku = this.multiCartesian(sku, attributions[i])
            }
          } 
        }
        return sku;
      }
    },
    created() {
      this.getMenuCategory()
      this.getMenuInfo()
      this.uploadUrl= window.HOST + 'order/menus/upload'
      this.uploadSpecUrl= window.HOST + 'order/menus/uploadSpec'
      this.config.serverUrl = window.HOST + 'admin/ueditor/upload'
      this.sku = this.getSku();
    },
    computed: {
      currentImage() {
        if(this.form.image){
          return window.HOST + this.form.image
        }
        return null
      }
    },
    watch: {
    },
    components: {
      'vue-ueditor-wrap':VueUeditorWrap
    },
    mixins: [http, fomrMixin]
  }
</script>
<style>
.el-form-item__content{
  line-height: 0;
}
.el-form-item .el-table .el-upload img{
  height:60px;
}
</style>



